<template>
    <div class="mt-10 d-flex flex-column">
        <div class="text-h4 font-weight-bold grey--text text--darken-3 mb-5">
            Your Configuration
        </div>

        <div class="grey--text mt-5 mb-5">
            Enter your Politics & War account information so BAR can login to your account.
        </div>

        <v-text-field
            label="Politics & War Email"
            v-model="config.pwEmail"
            outlined
        >
        </v-text-field>

        <v-text-field
            label="Politics & War Password"
            v-model="config.pwPassword"
            type="password"
            outlined
        >
        </v-text-field>

        <div class="grey--text mt-5 mb-5">
            Enter your Politics & War API key so BAR can access the API. You can find this <a href="https://politicsandwar.com/account">here</a>.
        </div>

        <v-text-field
            label="API Key"
            v-model="config.apiKey"
            outlined
        >
        </v-text-field>

        <div class="grey--text mt-5 mb-5">
            How often would you like to check for new nations, we recommend 3 minutes. You can choose for up to 24 hours to pass before checking.
        </div>

         <v-menu
            ref="timeMenu"
            v-model="timeMenu"
            :close-on-content-click="false"
            :nudge-right="40"
            :return-value.sync="time"
            transition="scale-transition"
            offset-y
            max-width="290px"
            min-width="290px"
        >
        <template v-slot:activator="{ on, attrs }">
          <v-text-field
            v-model="time"
            label="How often to check"
            prepend-icon="mdi-timer-outline"
            outlined
            readonly
            v-bind="attrs"
            v-on="on"
          ></v-text-field>
        </template>
        <v-time-picker
          v-if="timeMenu"
          format="24hr"
          v-model="time"
          full-width
          @click:minute="$refs.timeMenu.save(time)"
        ></v-time-picker>
      </v-menu>

       <div class="grey--text mt-5 mb-5">
           Choose what you would like your subject and message to be.
       </div>

       <v-text-field
            label="Message Subject"
            v-model="config.subject"
            outlined
       >
       </v-text-field>

       <v-textarea
          v-model="config.message"
          label="Message Body"
          outlined
        >
        
        </v-textarea>

        <div class="ml-auto mb-8">
            <v-btn outlined @click="loadData()">
                Discard Changes
            </v-btn>
            <v-btn color="primary" raised class="ml-5" @click="saveData()">
                Save
            </v-btn>
        </div>
    </div>
</template>

<script>
export default {
    name: 'Configuration',

    data()
    {
        return {
            config:
            {
                pwEmail: "",
                pwPassword: "",
                apiKey: 0,
                checkTime: 0,
                checksToRelogin: 0,
                subject: "",
                message: ""
            },
            timeMenu: null,
            time: "00:03",
        }
    },
    
    methods:
    {
        async get(url)
        {
            return new Promise((resolve, reject) => 
            {
                var request = new XMLHttpRequest();

                request.onreadystatechange = () => 
                {
                    if (request.readyState == 4)
                    {
                        if (request.status == 200)
                        {
                            var response = JSON.parse(request.responseText);

                            resolve(response);
                        }
                        else
                        {
                            reject(request);
                        }                            
                    }
                }

                request.open("GET", url, true);
                request.send({});

            });
        },

        async post(url, options)
        {
            return new Promise((resolve, reject) => 
            {
                var request = new XMLHttpRequest();

                request.onreadystatechange = () => 
                {
                    if (request.readyState == 4)
                    {
                        if (request.status == 200)
                        {
                            var response = JSON.parse(request.responseText);

                            resolve(response);
                        }
                        else
                        {
                            reject(request);
                        }                            
                    }
                }

                request.open("POST", url, true);
                request.setRequestHeader("Content-Type", "application/x-www-form-urlencoded");
                
                request.send(options);

            });
        },

        async loadData()
        {
            var config = await this.get('/api/config').catch();
            this.config = config;
        },

        async saveData()
        {
            this.post('/api/config', `data=${encodeURIComponent(JSON.stringify(this.config))}`);
        }
    },

    mounted()
    {
        this.loadData();
    }
}
</script>